@use 'sass:math';

%svg-common {
	background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-logo-footer {
	@extend %svg-common;
	background-position: 0 0;
	background-size: math.div(68, 68)*100% auto;
	width: 68px;

}
@mixin svg-pos-image-logo-footer {
	background-position: 0 0;
}
@mixin svg-image-logo-footer-hover {
	.svg-image-logo-footer {
		@include svg-pos-image-logo-footer-hover
	}
}

.svg-image-logo-footer:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(63, 68)*100%;
}

.svg-image-logo-footer-dims {
	width: 68px;
	height: 63px;
}


.image-by-height { width:auto; }
.image-by-height:before { content:none; }
.image-by-height > svg { height: inherit; }
.image-by-height > img { height: inherit !important; vertical-align: top !important; margin: 0 !important; padding: 0 !important; max-width: none !important; max-height: none !important; border: 0 !important; opacity: 0 !important; }