@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
  
* 
    box-sizing: border-box
    // min-height: 0.001vw; // — Fixes Safari bug with viewport units in calc()
html
    scroll-behavior: smooth
    background: $blue-dark-color

body
    overflow: hidden
    background: $blue-dark-color
    font-family: $durk-font
    color: $white-color
    width: 100%
    height: 100%
    min-height: 100vh
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    line-height: 1.5
    overflow-x: hidden
    &.locked 
        overflow: hidden!important


button
    padding: 0
    background-color: transparent
    border: none
    cursor: pointer
    line-height: 100%

h1, h2, h3, h4 
    margin: 0
    font-weight: normal
    font-weight: 300
    line-height: 100%

img, video
    width: auto 
    max-width: 100%
    width: 100%
    object-fit: cover

iframe, img, svg
    max-width: 100%
    vertical-align: top

p
    margin: 0

ul
    margin: 0
    padding-left: 0

li
    list-style-type: none

a
    text-decoration: none

.container
    max-width: 1280px 
    padding: 0 20px 
    margin: 0 auto
    @include xxs 
        padding: 0 15px

.scroll-container
    height: 100vh
    overflow-y: scroll
    scroll-snap-type: y mandatory
    position: relative
