.svg-image-logo-footer {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-logo-footer {
  background-position: 0 0;
  background-size: 100% auto;
  width: 68px;
}

.svg-image-logo-footer:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 92.6470588235%;
}

.svg-image-logo-footer-dims {
  width: 68px;
  height: 63px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  background: #1A1F32;
}

body {
  overflow: hidden;
  background: #1A1F32;
  font-family: "Druk Cyr";
  color: #FFFFFF;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  overflow-x: hidden;
}
body.locked {
  overflow: hidden !important;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  line-height: 100%;
}

h1, h2, h3, h4 {
  margin: 0;
  font-weight: normal;
  font-weight: 300;
  line-height: 100%;
}

img, video {
  width: auto;
  max-width: 100%;
  width: 100%;
  object-fit: cover;
}

iframe, img, svg {
  max-width: 100%;
  vertical-align: top;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 576px) {
  .container {
    padding: 0 15px;
  }
}

.scroll-container {
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  position: relative;
}

.header {
  position: absolute;
  width: 100%;
  padding: 70px 0 50px 0;
  background-color: transparent;
  z-index: 5;
}
@media (max-width: 1600px) {
  .header {
    padding: 60px 0 40px 0;
  }
}
@media (max-width: 1440px) {
  .header {
    padding: 50px 0 30px 0;
  }
}
@media (max-width: 1024px) {
  .header {
    padding: 40px 0 20px 0;
  }
}
@media (max-width: 576px) {
  .header {
    padding: 30px 0 10px 0;
  }
}
.header__logo {
  display: inline-block;
  width: 160px;
}
@media (max-width: 1440px) {
  .header__logo {
    width: 150px;
  }
}
@media (max-width: 1280px) {
  .header__logo {
    width: 140px;
  }
}
@media (max-width: 1024px) {
  .header__logo {
    width: 130px;
  }
}
@media (max-width: 768px) {
  .header__logo {
    width: 120px;
  }
}
@media (max-width: 576px) {
  .header__logo {
    width: 110px;
  }
}
@media (max-width: 480px) {
  .header__logo {
    width: 100px;
  }
}

.svg-item {
  width: 36px;
  height: 36px;
}
@media (max-width: 1440px) {
  .svg-item {
    width: 34px;
    height: 34px;
  }
}
@media (max-width: 1024px) {
  .svg-item {
    width: 32px;
    height: 32px;
  }
}
@media (max-width: 768px) {
  .svg-item {
    width: 30px;
    height: 30px;
  }
}

.top {
  scroll-snap-align: center;
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}
.top-figure {
  position: absolute;
  top: 0;
  left: 0;
}
.top-figure img {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  z-index: 0;
}
.top-picture {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;
}
@media (max-width: 1440px) {
  .top-picture {
    margin: 0 -10% 0 0;
  }
}
@media (max-width: 1150px) {
  .top-picture {
    margin: 0 -20% 0 0;
  }
}
.top-picture__big {
  display: block;
  max-width: 100%;
  height: 100%;
}
@media (max-width: 1150px) {
  .top-picture__big {
    height: 85vh;
  }
}
@media (max-width: 992px) {
  .top-picture__big {
    height: 80vh;
  }
}
@media (max-width: 768px) {
  .top-picture__big {
    height: 70vh;
  }
}
@media (max-width: 576px) {
  .top-picture__big {
    display: none;
  }
}
.top-wrapper {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  z-index: 10;
}
.top__title {
  max-width: 920px;
  font-family: "Druk Cyr";
  font-weight: 900;
  font-style: italic;
  font-size: 58px;
  line-height: 1.2;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #FFFFFF;
  margin-bottom: 50px;
}
@media (max-width: 1440px) {
  .top__title {
    max-width: 730px;
    font-size: 48px;
    margin-bottom: 40px;
  }
}
@media (max-width: 1024px) {
  .top__title {
    font-size: 40px;
    margin-bottom: 30px;
  }
}
@media (max-width: 992px) {
  .top__title {
    max-width: 550px;
    margin-top: 60px;
  }
}
