.header
    position: absolute
    width: 100%
    padding: 70px 0 50px 0
    background-color: transparent
    z-index: 5
    @include xl
        padding: 60px 0 40px 0
    @include lg
        padding: 50px 0 30px 0
    @include sm 
        padding: 40px 0 20px 0
    @include xxs
        padding: 30px 0 10px 0
    &__logo
        display: inline-block
        width: 160px
        @include lg
            width: 150px
        @include md
            width: 140px
        @include sm
            width: 130px
        @include xs
            width: 120px
        @include xxs
            width: 110px
        @include xxxs
            width: 100px

.svg-item
  width: 36px
  height: 36px
  @include lg
    width: 34px
    height: 34px
  @include sm
    width: 32px
    height: 32px
  @include xs
    width: 30px
    height: 30px
    
.top
  scroll-snap-align: center
  position: relative
  height: 100vh
  width: 100vw
  overflow-x: hidden
  &-figure
    position: absolute
    top: 0
    left: 0
    @include xxs
      
    & img
      height: 100vh
      width: 100vw
      object-fit: cover
      z-index: 0
  &-picture
    position: absolute
    bottom: 0
    right: 0
    z-index: 1
    @include lg
      margin: 0 -10% 0 0 
    @include mdx
      margin: 0 -20% 0 0 
    &__big
      display: block
      max-width: 100%
      height: 100%
      @include mdx
        height: 85vh
      @include smx
        height: 80vh
      @include xs
        height: 70vh
      @include xxs
        display: none
  &-wrapper
    position: relative
    height: 100vh
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: center
    z-index: 10
  &__title
    max-width: 920px
    font-family: $durk-font
    font-weight: 900
    font-style: italic
    font-size: 58px
    line-height: 1.2
    text-transform: uppercase
    letter-spacing: 2px
    color: $white-color
    margin-bottom: 50px
    @include lg
      max-width: 730px
      font-size: 48px
      margin-bottom: 40px
    @include sm
      font-size: 40px
      margin-bottom: 30px
    @include smx
      max-width: 550px
      margin-top: 60px
    @include xs
      max-width: 400px
      font-size: 32px
    @include xxxs
      margin-top: 30px
      max-width: 100%
      font-size: 30px
    &--color
      color: $black-light-color
  &__items
    max-width: 700px
    display: grid
    grid-template-columns: repeat(2, auto)
    grid-gap: 35px
    @include lg
      grid-gap: 30px
    @include sm
      grid-gap: 25px
    @include smx
      grid-template-columns: repeat(1, auto)
  &__item
    display: flex
    align-items: center
    &-img
      position: relative
      display: flex
      justify-content: center
      align-items: center
      width: 65px
      height: 65px
      background-color: $black-color
      border-radius: 8px
      transform: skew(-7deg)
      margin-right: 15px
      @include lg
        width: 60px
        height: 60px
      @include sm
        width: 55px
        height: 55px
      @include xs
        margin-right: 10px
        width: 50px
        height: 50px
      img
        transform: skew(7deg)
    &-text
      max-width: 250px
      font-family: $poppins-font
      font-weight: 600
      font-size: 24px
      line-height: 1.2
      text-transform: capitalize
      @include lg
        font-size: 22px
      @include sm
        font-size: 20px
      @include xs
        font-size: 18px

.bottom
  scroll-snap-align: center
  position: relative
  width: 100vw
  height: 100vh
  overflow-x: hidden
  &-figure
    position: absolute
    top: 0
    left: 0
    & img
      height: 100vh
      width: 100vw
      object-fit: cover
      z-index: 0
  &-picture
    position: absolute
    bottom: 0
    left: 0
    z-index: 1
    @include xl
      left: -10%
    @include lg
      left: -20%
    @include smx
        left: -5%
    &__big
      display: block
      max-width: 100%
      height: 100%
      @include md
        max-width: 90%
      @include mdx
        max-width: 80%
      @include sm
        max-width: 70%
      @include smx
        max-width: 60%
      @include xxs
        max-width: 100%
      @include zxs
        max-width: 100%
  &-wrapper
    position: relative
    height: 100vh
    display: flex
    flex-direction: column
    align-items: flex-end
    justify-content: center
    z-index: 10
    @include sm
      padding-top: 100px
      justify-content: flex-start
    @include smx
      padding-top: 70px
    @include xs
      padding-top: 50px
    @include xxs
      padding-top: 0
      justify-content: center
    @include wxs
      padding-top: 50px
      justify-content: flex-start
  &__title
    max-width: 630px
    font-family: $durk-font
    font-weight: 900
    font-style: italic
    font-size: 58px
    line-height: 1.2
    text-transform: uppercase
    letter-spacing: 5px
    color: $white-color
    margin-bottom: 40px
    @include lg
      font-size: 48px
      margin-bottom: 30px
    @include sm
      max-width: 600px
      font-size: 40px
    @include xs
      margin-bottom: 20px
      font-size: 32px
    @include xxxs
      font-size: 30px
    &--color
      color: $orange-color
  &__context
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
  &__subtitle
    max-width: 560px
    margin-bottom: 30px
    font-family: $poppins-font
    font-size: 20px
    line-height: 1.3
    font-style: italic
    text-transform: capitalize
    @include lg
      margin-bottom: 25px
    @include xs
      margin-bottom: 20px
      font-size: 18px
    @include xxs
      margin-bottom: 10px
  &__button
    position: relative
    display: flex
    justify-content: center
    align-items: center
    background-color: $orange-color
    border-radius: 8px
    transform: skew(-7deg)
    margin-top: 30px
    box-shadow: $box-shadow-dark-blue
    transition: $transition-main
    @include xs
      margin-top: 20px
    &:hover
      box-shadow: $box-shadow-orange
    &-link
      padding: 30px 60px
      cursor: pointer
      @include xs
        padding: 15px 30px
    &-text
      font-family: $durk-font
      font-weight: 900
      font-style: italic
      font-size: 18px
      text-transform: uppercase 
      letter-spacing: 3px
      color: $white-color
      @include xs
        font-size: 16px
        letter-spacing: 1px
  .footer
    position: absolute
    bottom: 0
    right: 0
    margin-bottom: 65px
    @include xl
      margin-bottom: 60px
    @include lg
      margin-bottom: 50px
    @include md 
      margin-bottom: 40px
    @include sm
      margin-bottom: 30px
    @include xs
      display: none
    &__logo
      display: inline-block
      width: 245px
      @include md
        width: 225px
      @include sm
        width: 200px
